import { AuthGuard } from './services/auth.guard';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DragulaModule, DragulaService } from 'ng2-dragula/ng2-dragula';

import { RouteModule } from './RouteModule/route.module';

import { TasksService } from './services/tasks.service';
import { AuthService } from './services/auth.service';

import { AppComponent } from './app.component';
import { RegComponent } from './components/reg/reg.component';
import { LoginComponent } from './components/login/login.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { IndividualTasksComponent } from './components/dashboard/individual-tasks/individual-tasks.component';
import { AddTasksModalComponent } from './components/dashboard/individual-tasks/add-tasks-modal/add-tasks-modal.component';

@NgModule({
  declarations: [
    AppComponent,
    RegComponent,
    LoginComponent,
    DashboardComponent,
    IndividualTasksComponent,
    AddTasksModalComponent
  ],
  entryComponents:[AddTasksModalComponent],
  imports: [
    BrowserModule,
    RouteModule,
    FormsModule,
    HttpModule,
    NgbModule.forRoot(),
    DragulaModule
  ],
  providers: [AuthService,AuthGuard,TasksService,DragulaService],
  bootstrap: [AppComponent]
})
export class AppModule { }



